import React from 'react';

import Window from './Window.js';

import '../static/css/terminal.css';

class TerminalWindow extends Window
{
	constructor()
	{
		super({
			closable: false,
			draggable: false,
			pos: {
				x: 'center',
				y: 'center',
			},
			background_color: '#2E3436',
			window_title: 'joseph@jnorman.dev -- ~/portfolio',
		});

		this.setupCommands();

		this.setState({
			commands: []
		});
	}

	onEnter(e)
	{
		if(e.keyCode ===13)
		{
			this.executeCommand(e.target.value);
		}
	}

	executeCommand(commandText)
	{
		console.log(commandText);
	}

	setupCommands()
	{
		this.commandTemplates = new Map();
		this.commandTemplates.set('help', [
			
		]);
	}

	renderContent()
	{
		return (
			<div className="terminal">
				<div className="terminal-command">
					<div className="terminal-command-query">joseph@jnorman.dev:~$</div>
					<input className="terminal-command-prompt" value="help" readOnly></input>
					<div className="terminal-query-output">this is s  ome test output blah lbah blah</div>
					<div className="terminal-query-output">this is some more test output</div>
				</div>
				<div className="terminal-command">
					<div className="terminal-command-query">joseph@jnorman.dev:~$</div>
					<input className="terminal-command-prompt" onKeyDown={ this.onEnter } autoFocus></input>
				</div>
			</div>
		);
	}
}

export default TerminalWindow;